.box-states{
 .d-inline-block{
   margin:5px 10px 10px;
   label{
     margin:0;
   }
 }
}
.box-coll-expand{
  a{
    margin-right:10px;
  }
}
.box-devisions-content{
  &.collapse{
    display: block;
    .devisions-content-header .title-top{
      &:before{
        border-width: 5px;
        border-top-color: transparent;
        border-right-color: transparent;
        border-bottom-color: transparent;
        border-left-color: #536c79;
        position: absolute;
      }
    }
    .devisions-content{
      ul{
        display: none;
      }
    }
  }
  margin-top:10px;
  .devisions-content-header{
    .title-top{
      position: relative;
      padding-left:15px;
      &:before{
        content: '';
        width: 0;
        height: 0;
        position: absolute;
        border-width: 4px;
        border-style: solid;
        border-top-color: transparent;
        border-right-color: #536c79;
        border-bottom-color: #536c79;
        border-left-color: transparent;
        position: absolute;
        left:0;
        top:5px;
      }
    }
  }
  .devisions-content{
    ul{
      margin:10px 0 0 30px;
      padding:0;
      li{
        list-style: none;
        margin:10px 0;
      }
    }
  }
}
.rat-list{
  margin-top:20px;
  .rat-list-item{
    margin:0 15px;
  }
}
.service-list-content{
  .item-service{
    margin:0 15px;
    display: inline-block;
  }

}
.box-states{
  ul{
    margin:0;
    padding:0;
    li{
      list-style: none;
      display: inline-block;
      margin:0 15px;
