.btn-add{
  background-color: #2e75b6;
  border-color: #2e75b6;
  font-size: 14px;
  min-width: 120px;
}
.btn-default{
  background-color: #fff;
  border-color: #2e75b6;
  font-size: 14px;
  min-width: 120px;
}
.btn-cancel{
  margin-right: 10px;
  &:hover{
    background-color: #1b8eb7;
    color: #fff;
  }
}
.flex-row-end{
  flex-direction: row;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.margin-top-10{
  margin-top: 10px;
}
.margin-right-10{
  margin-right: 10px;
}
.text-bold{
  font-weight: bold;
}
.modal-custom{
  .modal-header{
    background-color: #464646;
    .modal-title{
      color: #ffffff;
      font-weight: normal;
    }
    .close{
      color: #ffffff;
      opacity: 1;
    }
  }
  .form-group{
    margin-bottom: 0.5rem;
  }
}
.organisation-content{
  .col-left{
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
  }
  .content-inner{
    //padding: 15px;
    padding-bottom: 30px;
  }
  .header-content{
    border-bottom: 2px solid #ececec;
    padding-bottom: 5px;
    .title{
      font-size: 20px;
      margin: 0;
    }
    .edit{
      color: #2e75b6;
      cursor: pointer;
      padding-left: 20px;
      padding-right: 20px;
      font-weight:bold;
    }
  }
  .wrap-table{
    overflow-x: auto;
  }
  .table-custom{
    width: 100%;
    margin-top: 30px;
    .td-last{
      width: 70%;
    }
    .td-col-40{
      width: 30%;
      display: inline-block;
    }
    .td-col-10{
      width: 75px;
      display: inline-block;
    }
  }
  .company-content{

  }
  .banking-content{
    .table-custom{
      margin-top: 0;
      td{
        padding: 10px 0;
        span{
          &:first-child{
            margin-right: 5px;
          }
        }
      }
    }
  }
  .super-fund-content{
    .table-custom{
      margin-top: 0;
      td{
      }
    }
  }
  .logo-content{
    .wrap-logo{
      margin-top: 20px;
      padding-right: 20px;
      padding-left: 20px;
    }
  }
}
@media (max-width: 768px) {
  .header-content{
    .col-left{
      padding-right: 0;
      .title{
        font-size: 18px;
      }
    }
    .col-right{
      .edit{
        padding-right: 0;
      }
    }
  }
  .table-custom{
    min-width: 500px;
    width: initial;
    &.medium{
      min-width: 768px;
    }
    &.large{
      min-width: 1000px;
    }

  }
}
.box-organisation{
  .header-organisation{
    display: block;
    overflow: hidden;
  p{
    float: left;
    font-size:18px;
  }
  .btn-primary{
    float: right;
  }
  }
  .organisation-content{
    overflow: hidden;
    display: block;
    margin-top:20px;
    a{
      display: block;
      margin-bottom:10px;
      padding:10px 0 0 40px;
    }
  }
}
.btn-cancel{
  background: #ececec;
}

.table-custom tr td{
  padding:0;
  line-height:50px;
}
.table-custom tr td .input-invalid-feedback{
  line-height:1;
  margin-bottom:10px;
}
.table-custom tr td input[type='text']{
  border:none;
  outline: none;
  padding: 0;
  vertical-align: top;
  height:50px;
  background: transparent;
  width:100%;
}
.table-custom tr td input[type='text']:focus{
  border-bottom:0.5px solid #20a8d8;
}

:root {
  --main-bg-color: red;
}
.one {
  color: white;
  background-color: var(--main-bg-color);
  margin: 10px;
  width: 50px;
  height: 50px;
  display: inline-block;
}