.box-setup{
  //border:1px solid #bfbfbf;
  overflow: hidden;
  padding:30px;
  min-height:calc(100vh - 219px);
  @media (max-width:768px) {
    padding:15px;
  }
}
.setup-content{
  .box-setup__info{
    overflow: hidden;
    margin-bottom:50px;
    width:33.333%;
    display: inline-block;
    margin-right:-3px;
    vertical-align: top;
    @media (max-width:1199px) {
    width:50%;
    }
    @media (max-width:768px) {
      width:100%;
      display: block;
      margin-right:0;
    }
    @media (max-width:480px) {
      text-align: center;
    }
    .info-icon{
      float: left;
      width:60px;
      margin-right:15px;
      margin-top:10px;
      @media (max-width:480px) {
        float: none;
        display: inline-block;
      }
      img{
        width:100%;
      }
    }
    .info-content{
      float: left;
      @media (max-width:480px) {
        float: none;
      }
      h3{
        font-size:16px;
        font-weight:800;
        margin-bottom:20px;

      }
      .routers-setup{
        display: block;
        clear: both;
        a{
          color: #2e76cb;
          font-size:14px;
          display: inline-block;
          margin-bottom:10px;
        }
        .division{

        }

      }
    }
  }
}