.wrap-content {
  position: relative;
  overflow: hidden;

}

.col-left {
  width: 200px;
}

#main {
  padding-left: 230px;
  margin-top: 86px;
  margin-bottom: 56px;
  @media (max-width: 1366px) {
    margin-top: 60px;

  }
  @media (max-width: 1199px) {
    padding-left: 0;
  }
  .main-content-inner {
    overflow: hidden;
    padding: 30px;
  }
}

button {
  outline: none;
}

.left {
  left: 0;
}

.left230 {
  left: 230px;
  @media (max-width: 1199px) {
    left: 0;
  }
}

.edit-box {
  display: none;
  &.active {
    display: inline-block;
  }
}

.box-logo-company {
  margin-top:10px;
  position: relative;
  i {
    font-size: 20px;
    cursor: pointer;
    position: absolute;
    top: -30px;
    color: red;
    right: 0;

  }
  &.active {

  }
}

.app-header.navbar .navbar-brand {
 // background-size: 45px auto !important;
}

.sidebar .nav-dropdown-toggle::before {
  content: none !important;
}

.btn-cancel {
  &:hover {
    color: #1b8eb7 !important;
    color: #fff !important;
    & + button {
      background: #ececec !important;
      color: #536c79;
    }
  }
}

.table-custom tr {
  th{
    padding: 0.75rem 0;
    border-bottom: 2px solid #c2cfd6;
  }
  td {
    border-bottom: 1px solid #c2cfd6;
    padding: 0.75rem 0;
    vertical-align: top;
    .btn-modal-banking{
      float: right;
    }
  }
}
.sheet-right-edit{
  a{
    margin-left:15px;
  }
}
.btn-modal-banking{
  display: block;
  visibility: hidden;
}
.breadcrumb.breadcrumb-fixed{
  position: fixed;
  z-index:234;
  width:100%;
}
.margin-content{
  margin-top:75px;
}
.card{
  .btn-edit-box{
    display: none;
  }
  &.edit-card{
    position: relative;
    .table-custom tr {
      &:hover {
        .btn-modal-banking {
          visibility: visible;
        }

      }
    }
    .btn-edit-box{
      display: block;
    }
    .btn-edit{
      display: none;
    }
    .card-header{
      .edit-box{
        display: inline-block;
      }
    }
    .box-logo-company{
      border: 4px solid rgb(194, 207, 214);

    }
    .btn-edit-box{
      display: block;
    }
    .edit-box{
      display: block;
    }
  }
}
ss-multiselect-dropdown{
  background: transparent;
  width: 100%;
  .dropdown-inline{
    display: block;
    width: 100%;
    .dropdown-toggle{
      width: 100%;
      background: transparent;
      text-align: left;
      &:after{
        float: right;
        margin-top:10px;
      }
    }
  }
  .dropdown-menu{
    width:100%;
    li{
      a{
        color: #151b1e;
      }
    }
  }
}