  .sider-bar-title{
    color: #fff;
    font-size:20px;
    padding:10px 20px;
    margin:0;
    i{
      margin-right:10px;
    }
  }
  .sidebar__list {
    .list-group-item {
      background: transparent;
      border:none;
      color: #fff;
      i{
        margin-right:10px;
      }

      &.has-sub-menu.collapsed{
        position: relative;
        &:before{
          background-image: url("data:image/svg+xml;charset=utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 11 14'><path fill='%23536c79' d='M9.148 2.352l-4.148 4.148 4.148 4.148q0.148 0.148 0.148 0.352t-0.148 0.352l-1.297 1.297q-0.148 0.148-0.352 0.148t-0.352-0.148l-5.797-5.797q-0.148-0.148-0.148-0.352t0.148-0.352l5.797-5.797q0.148-0.148 0.352-0.148t0.352 0.148l1.297 1.297q0.148 0.148 0.148 0.352t-0.148 0.352z'/></svg>");
          position: absolute;
          top: 50%;
          right: 1rem;
          display: block;
          width: 8px;
          height: 8px;
          padding: 0;
          margin-top: -4px;
          content: "";
          transform: rotate(180deg);

        }
      }
      &:hover{

        opacity:0.8;
      }
    }
  }
}
.icon-sidebar{
  background: #000;
  overflow: hidden;
  position: relative;
  height: 60px;
  width: 80px;
  border-radius: 5px;
  margin: 0 0 20px 0;
  display: none;
  @media (max-width:1199px) {
    display: block;
  }
}
.section-anchor{
  ul.nav-dropdown-items{
    li{
      a{
        padding-left:60px;
      }
    }
  }
}
.app-header .sidebar-minimizer{
  background: transparent;
  border: none;
  font-size: 20px;
  line-height: 1;
  min-width: 50px;
  padding: 0.25rem 0;
  outline: none;
}