.header{
  background: #165979;
  position: fixed;
  width:100%;
  top:0;
  z-index:34;
  @media (max-width:1365px) {
    height:60px;
  }
  .header-menu{
    padding:10px 20px;
    overflow: hidden;
    @media (max-width:1365px) {
      transition: all 0.3s ease-in-out;
      position: fixed;
      height: 100%;
      width: 300px;
      padding:30px 0;
      background: #165979;
      z-index:987;
      top:0;
      transform: translateX(-100%);
      &.open{
        transform: translateX(0);
      }
    }
    @media (max-width:1365px) {
      ul {
        float: none;
        display: block;
        li{
          display: block;
          float: none;
          text-align: left;
          margin:0;
          a{
            display: block;
            text-align: left;
            padding:10px 20px;
            transition-duration: 0.2s;
            &:hover{
              opacity:0.8;
            }
            img{
              width:30px;
              display: inline-block;
              margin-right:10px;
              vertical-align: middle;
            }
            span{
              display: inline-block;
              vertical-align: middle;
            }
          }
        }
      }
    }
    &__left{
      float: left;
      margin:0;
      padding:0;
      @media (max-width:1365px) {
        float: none;
        display: block;
      }
      
      li{
        float: left;
        margin:0 15px;
        text-align: center;
        list-style: none;
        @media (max-width:1365px) {
          float: none;
          text-align: left;
        }
        a{
          text-align: center;
          display: block;

          img{
            @media (max-width:1365px) {
              display: inline-block;
            }
          }
          span{
            display: block;
            font-size:14px;
            color: #fff;
          }
        }
      }
    }
    &__right{
      float: right;
      margin:0;
      padding:0;
      li{
        float: left;
        text-align: center;
        list-style: none;
        margin:0 15px;
        a{
          text-align: center;
          display: block;
          span{
            display: block;
            font-size:14px;
            color: #fff;
          }
        }
      }
    }
  }
}
.sub-header{
  background: #404040;
  padding:15px 30px;
  margin-top:86px;
  @media (max-width:1365px) {
    margin-top:60px;
  }
  &__title{
    color: #fff;
    font-size:20px;
    font-weight:400;
    margin:0;
  }
}
