.filter-list {
  > * {
    display: inline-block;
  }
  > input {
    display: inline-block;
    width: 200px;

  }
  > .btn {
    vertical-align: top;
  }
}

.status-color {

  display: inline-block;
  > p {
    display: inline-block;
    margin: 0 20px 0 0;

  }
  .item-color {
    display: inline-block;
    margin-right: 15px;
    p {
      display: inline-block;
      vertical-align: middle;
      margin: 0;
      padding: 0;
    }
    .color-stauts-1 {
      height: 15px;
      width: 15px;
      display: inline-block;
      vertical-align: middle;
      margin-right: 5px;
    }
  }
}

.status-right {
  float: right;
  p {
    margin-bottom: 0;
  }

}

.edi-header {
  margin: 20px 0;
}

.box-list-edi {
  .edi-header {
    .edi-head-left {
      p {
        display: inline-block;
        margin: 0;
        line-height: 40px;
        &.text-1 {
          font-weight: 700;
          font-size: 20px;
        }
        &.text-2 {
          color: #20a8d8;
        }
      }
    }
  }
}

.col-process {
  text-align: right;
}

.edi-text-c {
  margin: 0;
  line-height: 40px;
  font-size: 20px;
  text-align: center;
}
.box-list-edi{
  margin-bottom:30px;
}
.table-edi {

  p {
    margin: 0;
  }
  tr {
    td, th {
      padding: 5px;
      text-align: center;
      color: #151b1e;
      vertical-align: middle;
      background: #d9d9d9;
      border:2px solid #c2cfd6;
    }
    th, td {
      font-size: 14px;
      font-weight: 600;


    }
  }
  thead {
    tr {
      td {

      }
    }
  }
  tbody {
    tr {
      td {
        .time {
          text-align: right;
          margin-top: 20px;
          display: block;
        }
        .div-status {
          text-align: right;
          width: 10px;
          height: 10px;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          border-radius: 50%;
          background: #5b9bd5;
          float: right;
          margin-top: 10px;
        }
        .box-comment{
          text-align: right;
        }
        &:first-child {
          width: 200px;
          background: #fff;
        }
        .icon-comment{
          width:30px;
          float: right;
        }
      }
    }
  }
  .bg-un-confirm {
    background: #ffc000;
  }
  .bg-un-asigned {
    background: #ff0000;
  }
  .bg-assigned {
    background: #bdd7ee;
  }
  .bg-verified {
    background: #a9d18e;
  }
  .bg-double-shift {
    background: #ffd966;
  }
  .bg-different-site {
    background: #fbe5d6;
  }
}

.bg-un-confirm {
  background: #ffc000;
}

.bg-un-asigned {
  background: #ff0000;
}

.bg-assigned {
  background: #bdd7ee;
}

.bg-verified {
  background: #a9d18e;
}

.bg-double-shift {
  background: #ffd966;
}

.bg-different-site {
  background: #fbe5d6;
}

.item-day-status{

  padding:5px 30px;
  .content-slider{
    position: relative;
    height:50px;
    overflow: hidden;
    .line-slider{
      background: #5b9bd5;
      height:1px;
      width:100%;
      margin-top:25px;
    }
    .dot-slier{
      width:15px;
      height:15px;
      border:1px solid #000;
      -webkit-border-radius: 100%;
      -moz-border-radius: 100%;
      border-radius: 100%;
      background: #5b9bd5;
      &.dot-left{
        left:0;
        top:18px;
        position: absolute;
      }
      &.dot-center{
        left:50%;
        top:18px;
        position: absolute;
      }
      &.dot-right{
        right:0;
        top:18px;
        position: absolute;
      }
    }
  }
}
.bg-td-day{
  background: #ff8181 !important;
}
.info-employee-day{
  .division-double-shift{
    height:2px;
    background: #c2cfd6;
    margin:5px -5px;
  }
}
.info-employees-popup{
  margin-bottom:20px;
}
.header-filter{
  background: #f2f2f2;
  border:1px solid #dedede;
  width:100%;
  padding:10px;
  overflow: hidden;
  > p{
    margin:0;
    float: left;
  }
  .header-toggle{
    float: right;
  }
  .btn-primary{
    margin-right:20px;
  }
  
}
.info-top-row{
  padding:10px 0;
  p{
    margin-bottom:7px;
    span{
      display: inline-block;
      min-width:150px;
    }
  }
}
.infoFilter {
  margin:10px 0;
  .form-control{
    margin-bottom:20px;
  }
}
.box-day-of-week{
  .all-day,.days-right{
    display: inline-block;
    margin:0 10px;
    p{
      margin:0;
    }
    .all-day{
      margin-right:20px;
    }
  }
}
.box-roster-comment{
  textarea{
    width:100%;
    height:100px;
  }
}
.box-filter{
  >p,.filter-list {
    display: inline-block;
    margin-right:20px;
  }
  .filter-list > *{
    margin-bottom:5px;
  }
  .select-filter{
    button.multiselect{
      border:1px solid #c2cfd6;
    }
  }


}
.box-popup-right{
  float: right;
  .btn-info{
    color: #fff;
    margin:0 5px;
  }
  .nav.nav-tabs{
    border:none;
    li{
      a{
        background: transparent;
        color: #ffffff;
        border:1px solid #fff;

        margin:0 5px;
        &.active{
          background: #0059fd;
          border:1px solid transparent;
        }
        &:focus{
          background: #0059fd;
        }
      }
    }
  }
}
.box-list-weeks{
  padding:15px;
  ul{
    li{
      list-style: none;
      display: inline-block;
      vertical-align: top;
      position: relative;
      a{

      }
      .sub-week{
        position: absolute;

        left:0;
      }
    }
  }
}
.bnt-add-week{
  padding:0.5rem 1rem;
  line-height:40px;
}
.ad-hoc-content{
  .info-week{
    margin-bottom:20px;
  }
}
.collapse-box{
  &:before{
    content: "-";
  }
  &.collapsed{
    &:before{
      content: "+";
    }
  }
}
.tab-content.content-addshift{
  border:none;
  > .tab-pane{
    padding:0;
  }
  .card-body{
    padding:1rem;
  }
}
.ood-demo{
  div {
    cursor: move;
    width:70px;
    height:70px;
    background:black;
    z-index: 1;
    margin:2px;
    float:left;
  }
  .active {
    background: grey;
    z-index: 2;
  }
}
.calendar-shift-cal .shift-cal_table{
  position: relative;
  tr{
    td{
      position: relative;
      .event{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: left;
        text-align: center;
        padding: 24px 0;
        line-height: 100%;
      }
    }
  }
}
.collapse-box.text-left{
  float: left;
}
.modal-body__addhoc{
  height:80vh;
  overflow-y: scroll;
}
@media (max-width:1365px) {
  .edi-body{
    width:100%;
    overflow-x: scroll;
    .table-edi{
      width:1500px;
    }
  }
}
@media (max-width:991px) {
#modalAddShift .card-body{
  .col-3,.col-9{
    width:100%;
    max-width:100% !important;
    text-align: left !important;
    flex:none;
  }
}
}