.themes-setting_options {
  margin-bottom:20px;
  .setting-title {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  .setting-color-content {
    padding-left: 20px;
    &.set-bg-header{
      .active{
        padding: 5px;
        border: 2px solid #e2e2e2;
      }
    }
    input {
      display: inline-block;
      border: none;
      width: 35px;
      height: 20px;
      vertical-align: top;
      margin-right: -4px;
      cursor: pointer;
      pointer-events: none;
      &:focus {
        padding: 5px;
        border: 2px solid #e2e2e2;
      }
    }
  }
}
.card.edit-card{
  .themes-setting_options .setting-color-content input{
    pointer-events:auto ;
  }
}
